import React, { Component } from 'react';
import MediaCapturer from 'react-multimedia-capture';


class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {login : '', password : ''}

        this.handleLoginChange = this.changeLogin.bind(this);
        this.handlePasswordChange = this.changePassword.bind(this);
    }

    changeLogin(event) {
        this.setState({ login : event.target.value })
    }

    changePassword(event) {
        this.setState({ password : event.target.value })
    }

    render () {
        return (
            <form>
                <h2>Login</h2>
                Login <input type="text" value={this.state.login} onChange={this.handleLoginChange}/> <br/>
                Password <input type="password" value={this.state.password} onChange={this.handlePasswordChange}/> <br/>
                <input type="button" value="submit" onClick={this.props.login.bind(this.props.parent, this.state.login, this.state.password)}/>
            </form>
        )
    }
}

class Recording extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			granted: false,
			rejectedReason: '',
			recording: false,
            paused: false,
            job_name: undefined
		};

		this.handleStart = this.handleStart.bind(this);
		this.handleStop = this.handleStop.bind(this);
		this.handleStreamClose = this.handleStreamClose.bind(this);
		
    }
    
  
    authenticated2(jobname) {
        this.setState(() => {
            return {job_name: jobname};
          });    
        console.log("JOB_NAME RECORDING ---> " + this.state.job_name)
    }

    failedauthenticated2() {
        this.setState(() => {
            return { token : undefined, errormessage : 'Authentication Error' };
          });    
    }
    
    
	handleStart(stream) {
		this.setState({
			recording: true
		});

		console.log('Recording Started.');
	}
	handleStop(blob) {
        //blob tem o audio
        // fazer o post aqui
		this.setState({
			recording: false
		});

        console.log('Recording Stopped.');
        console.log(blob);
        //this.downloadAudio(blob);
        
        //post e fetch da info na lambda func do API Gateway AWS

		console.log("auth token in recorder --> " + this.props.token );
        
        //pack recording to POST
        
        let send_rec = new FileReader();
        send_rec.readAsDataURL(blob);
        send_rec.onloadend = () => {
            var dataUrl = send_rec.result;
            var base64data = dataUrl.split(',')[1];
            console.log("datUrl " + dataUrl);
            console.log("ENCODED ---- " + base64data);   
         
            
            //fetch

            var theobject = this
            var formparameters = {
                method: 'POST', // or 'PUT'
                body: JSON.stringify({'token' : this.props.token, 'audio_encoded' : base64data}),
                headers:{
                'Content-Type': 'application/json',
                'authorizationToken' : JSON.stringify({ 'token' : this.props.token })
                }
            }
            
            console.log(formparameters);
            console.log('trancriber server ->  ' + this.props.transcriberserver);

            fetch(this.props.transcriberserver, formparameters).then(function(data) {
                
                        if(data.status!==200) {
                            theobject.failedauthenticated2()
                            throw new Error(data.status)
                        }
                        else {
                            var json = data.json();
                            return json;
                        }
            }).then((thetoken) => {
                        console.log('message =', thetoken)
                        if ('token' in thetoken){
                            console.log("THETOKEN " + thetoken['TranscriptionJobName'])
                            console.log(theobject)
                            theobject.authenticated2(thetoken['TranscriptionJobName'])
                            this.props.jobnameCallback(this.state.job_name)
                        }
            }).catch(function(error) {
                        console.log('There has been a problem with your fetch operation: ', error.message)
                    });   
        }
    }


	handleStreamClose() {
		this.setState({
			granted: false
		});
	}
	handleError(err) {
		console.log(err);
	}
	 
	render() {
		const recording = this.state.recording;
        
		return (
			<div ref="app">
				<h3>Recording your Destination</h3>
				<MediaCapturer
					constraints={{ audio: true }}
					mimeType="audio/webm"
					timeSlice={10}
					onStart={this.handleStart}
                    onStop={this.handleStop}
					onError={this.handleError} 
					onStreamClosed={this.handleStreamClose}
					render={({ start, stop }) => 
					<div>
						<p>Recording: {recording.toString()}</p>
						<button onClick={start}>Start</button>
						<button onClick={stop}>Stop</button>
					</div>
				}/>
			</div>
		);
	}
}

class Transcription extends Component{
    constructor(props) {
        super(props);
        this.state = { transcribed: undefined, timerid : undefined};

        this.handleDestinationChange = this.changeDestination.bind(this);
    }


    changeDestination(event) {
        this.setState({ transcribed : event.target.value })
    }

    authenticated(value) {
        console.log(value)
        this.setState(() => {
            return {transcribed: value};
          });    
        console.log("JOB_NAME TRANSCRIBED ---> " + this.state.transcribed)
    }


    tick() {
        // Call this function so that it fetch first time right after mounting the component
        var theobject = this
        var formparameters = {
            method: 'POST', // or 'PUT'
            body: JSON.stringify({'token' : this.props.token, 'job_name' : /*this.props.job_name*/ '04490e1b-22fe-4110-865a-6daa1c500b26.json'}),   //TODO QUANDO CONVERTER PARA MP3 ARRUMAR ISSO E ADICIONAR O .JSON NO SETSTATE DA RECORDER
            headers:{
            'Content-Type': 'application/json',
            'authorizationToken' : JSON.stringify({ 'token' : this.props.token })
            }
        }
        
        console.log(formparameters);
        console.log('trancriber server ->  ' + this.props.transcriptionserver);

        fetch(this.props.transcriptionserver, formparameters).then((data) => {
            
                    if(data.status!==200) {
                        throw new Error(data.status)
                    }
                    else {
                        var json = data.json();
                        this.timer = clearInterval(this.timer);
                        return json;
                    }
        }).then((thetoken) => {
                    console.log('message =', thetoken)
                    console.log(theobject)
                    theobject.authenticated(thetoken['transcription'])


        }).catch(function(error) {
                    console.log('There has been a problem with your fetch operation: ', error.message)
                }); 

        // set Interval
    }
    
    componentDidMount() {
        // Clear the interval right before component unmount
        this.timer = setInterval(() => this.tick(), 5000)
    }

    render() {

        if(this.state.transcribed != undefined){
            return (
                <div>
                    Location <input type="text" value={this.state.transcribed} onChange={this.handleDestinationChange}/><br/>
                    <input type="button" value="submit" onClick={this.props.getDestinationCallback.bind(this.props.parent, this.state.transcribed)}/>
                </div>
               
            );
        }
        else{
            return (
                <div>
                    <h2>Transcribing...</h2>
    
                </div>
               
            );
        }
      
    }
    
}

class Page extends Component {
    constructor(props) {
        super(props);
        this.state = {token : undefined, errormessage : undefined, job_name: undefined, destination: undefined};
    }

    authenticated(token) {
        this.setState({ token : token, errormessage : undefined })
    }

    failedauthenticated() {
        this.setState({ token : undefined, errormessage : 'Authentication Error' })
    }

    doLogin(login, password) {
        var theobject = this
        var formparameters = {
            method: 'POST', // or 'PUT'
            body: JSON.stringify({'login' : login, 'password' : password}),
            headers:{
              'Content-Type': 'application/json',
            }
        }
        console.log(formparameters);
        console.log("loginserver --> " + this.props.loginserver);
  fetch(this.props.loginserver, formparameters).then(function(data) {
            if(data.status!==200) {
                theobject.failedauthenticated()
                throw new Error(data.status)
            }
            else {
                var json = data.json();
                return json;
            }
  }).then(function(thetoken) {
            console.log('message =', thetoken)
            if ('token' in thetoken)
                theobject.authenticated(thetoken['token'])
  }).catch(function(error) {
            console.log('There has been a problem with your fetch operation: ', error.message);
        });

        console.log("TOKEN --> " + this.props.token);
    }

    getDestination(dest){
        this.setState(() => {
            return { destination: dest };
          });  
    }

    handleJobNameCallback(jobname){
        this.setState(() => {
            return { job_name: jobname };
          });  
    }

    render() {
        console.log("Destination : ",this.state.destination)
        if (this.state.errormessage != undefined)
            var errormessage = <h2>{this.state.errormessage}</h2>
        else
            var errormessage = <div/>
        if (this.state.token == undefined)
            return (
                <div>
                    {errormessage}
                    <Login parent={this} login={this.doLogin}/>
                </div>
            )
        if(this.state.job_name != undefined && this.state.destination == undefined){
            return(
                <div>
                    {errormessage}
                    <Transcription parent={this} getDestinationCallback={this.getDestination} transcriptionserver={this.props.transcriptionserver} token={this.state.token} job_name={this.state.job_name}/>
                </div>
            )
        }
        if(this.state.job_name != undefined && this.state.destination != undefined){
            return(
                <div>
                    {"ESTOU AQUI"}
                </div>
            )
        }
        else
            return (
                <div>
                    {errormessage}
                    <Recording jobnameCallback={this.handleJobNameCallback.bind(this)} transcriberserver = {this.props.transcriberserver} token = {this.state.token} job_name = {this.state.job_name}/>
                </div>
            )
    }
}

